$columns: 12;
$atom: (100% / $columns);

$break_xs: 320px;
$break_sm: 600px;
$break_md: 768px;
$break_lg: 992px;
$break_xl: 1440px;
$break_xxl: 1960px;

$offset: 1.875rem;
$offset_xs: $offset;
$offset_sm: $offset;
$offset_md: $offset;
$offset_lg: $offset;
$offset_xl: $offset;
$offset_xxl: $offset;

$offset_one_side: ($offset / 2);
$offset_xs_one_side: ($offset_xs / 2);
$offset_sm_one_side: ($offset_sm / 2);
$offset_md_one_side: ($offset_md / 2);
$offset_lg_one_side: ($offset_lg / 2);
$offset_xl_one_side: ($offset_xl / 2);
$offset_xxl_one_side: ($offset_xxl / 2);

$fields: 0.9375rem;

@mixin container-full() {
    padding-left: $fields;
    padding-right: $fields;
}

@mixin row-offsets() {
    margin-left: ($offset_one_side * -1);
    margin-right: ($offset_one_side * -1);
}

@mixin col-offsets($type) {
    #{$type}-left: $offset_one_side;
    #{$type}-right: $offset_one_side;
}

@mixin container() {
    max-width: 1340px;
    margin: 0 auto;
    @include container-full();
}

@mixin row-flex() {
    display: flex;
    flex-wrap: wrap;
    @include row-offsets();
}

@mixin row-float() {
    @include row-offsets();
    @include clearfix();
}

@mixin row-ib() {
    @include row-offsets();
}

@mixin col() {
    box-sizing: border-box;
    word-wrap: break-word;
    @include col-offsets(margin);
}

@mixin col-float() {
    float: left;
}

@mixin col-ib() {
    display: inline-block;
    vertical-align: top;
}

@mixin col-padding() {
    word-wrap: break-word;
    @include col-offsets(padding);
}

@mixin size($n) {
    $val: 100% / $columns * $n;
    width: #{calc(#{$val} - #{$offset})
}

;
}

@mixin size-xs($n) {
    $val: 100% / $columns * $n;
    @include xs(width, #{calc(#{$val} - #{$offset_xs})
}

);
}

@mixin size-sm($n) {
